//** Load fonts from this directory.
@icon-font-path: "../fonts/";
//** File name for all font files.
@icon-font-name: "iconfont";
//** Element ID within SVG icon file.
@icon-font-svg-id: "iconfont";

@font-face {
    font-family: 'evicon';
    /* project id 931572 */
    src: url('//at.alicdn.com/t/font_931572_siq3oydbr6f.eot');
    src: url('//at.alicdn.com/t/font_931572_siq3oydbr6f.eot?#iefix') format('embedded-opentype'),
        url('//at.alicdn.com/t/font_931572_siq3oydbr6f.woff2') format('woff2'),
        url('//at.alicdn.com/t/font_931572_siq3oydbr6f.woff') format('woff'),
        url('//at.alicdn.com/t/font_931572_siq3oydbr6f.ttf') format('truetype'),
        url('//at.alicdn.com/t/font_931572_siq3oydbr6f.svg#evicon') format('svg');
}

// Catchall baseclass
.evicon {
    position: relative;
    top: 1px;
    display: inline-block;
    font-family: "evicon";
    font-style: normal;
    font-weight: 400;
    // line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

